@import url(https://fonts.googleapis.com/css?family=Comfortaa&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);

p {
  font-family: "Open Sans", sans-serif;
}

.button-container {
  width: 334px;
  height: 59px;
  background: #ffb72d;
  border-radius: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
}

.button-container:hover {
  background: #cf9322;
  -webkit-transition: all ease-in-out 0.15s;
  transition: all ease-in-out 0.15s;
}

.button-text {
  color: white;
  font-family: "Open Sans", sans-serif;
  text-decoration: none;
}

.link-button {
  color: red;
}

.entrance-container {
  background: url(/park-pass/static/media/john-matychuk-yvfp5YHWGsc-unsplash.5f5d2868.jpg);
  background-size: cover;
  width: 100%;
  height: 100vh;
  position: absolute;
  left: 0px;
  top: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.entrance-banner {
  width: 60%;
  height: 70%;
  background: rgba(196, 196, 196, 0.5);
  border-radius: 40px;
  padding: 32px;
}

.entrance-banner-title {
  font-family: "Comfortaa", cursive;
  color: white;
  font-size: 40px;
}

.input-container {
}

.input-label {
  color: white;
  font-family: "Open Sans";
  font-size: 13px;
  margin-bottom: 2px;
}

.input-input {
  border: none;
  border-bottom: 1px solid white;
  outline: none;
  width: 100%;
  max-width: 600px;
  font-size: 17px;
  color: white;
  background-color: transparent;
}

.car-icon {
  width: 230px;
  height: 230px;
}

.home-container {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: url(/park-pass/static/media/ivana-cajina-WPVtT0MEM00-unsplash.a64433de.jpg);
  background-size: cover;
}

.home-banner {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 50rem;
  height: 35rem;
}

.home-badge {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 20rem;
  height: 20rem;
  background: #c4c4c4;
  border-radius: 40px;
}

.home-badge:hover {
  background: #bbbbbb;
  -webkit-transition: all ease-in-out 0.1s;
  transition: all ease-in-out 0.1s;
}

.home-badge-text {
  font-size: 30px;
  color: white;
  text-decoration: none;
}

.home-banner-title {
  font-family: "Comfortaa", cursive;
  color: white;
  font-size: 50px;
  margin-bottom: 0px;
}

.home-banner-container {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  width: 50rem;
  height: 35rem;
  background: rgba(196, 196, 196, 0.5);
  border-radius: 40px;
}

